import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-expense-pay-invoice',
  templateUrl: './expense-pay-invoice.component.html',
  styleUrls: ['./expense-pay-invoice.component.scss']
})
export class ExpensePayInvoiceComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  isInvoiceSelected:boolean = false;
  selectAllInvoices:boolean = false;

  totalAmountArray:any;
  selectedInvoiceIdArray:any;

  vendorDataList:any;
  isVendorDataLoaded:boolean = false;
  vendorCategoryData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  columnData:any ;
  gridSourceData:any;

  timeZone:any;

  constructor(
    private route: ActivatedRoute,
    private lookupService: LookupService,
  	private accountsService:AccountsService,
    private vendorService:VendorService,
    private sharedService:SharedService,
  	private sessionService:SessionService
  ) { }

  

  getInvoiceDate(date){
  	return moment(date).add(this.timeZone.offset, 'hours').format(this.timeZone.date);
  }

  getAccountName(account){

    var accountDetails = {
      glaccountId: 0
    };

    if(account == undefined || account.length == 0){
      accountDetails.glaccountId = 0;
    }
    else {
      accountDetails.glaccountId = account[0].glaccountId;
    }

  	var data = _.filter( this.glAccountListData, function( item ) {
      if(item.glaccountId === accountDetails.glaccountId)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].glaccountName;
    }

  	return '';
  }


  getCategoryName(id){
    
    var data = _.filter( this.vendorCategoryData, function( item ) {
      if(item.lookupValueId === parseInt(id))
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }


  getAllInvoices(){

    this.totalAmountArray = [];
    this.selectedInvoiceIdArray = [];

    if(this.selectAllInvoices){
      _.each(this.invoiceDataList, (item)=>{
        item.checked = true;
        this.totalAmountArray.push(item.vendorInvoiceAmount)
        this.selectedInvoiceIdArray.push(item.vendorInvoiceId);
      })
      this.isInvoiceSelected = true;
    }
    else{
      _.each(this.invoiceDataList, (item)=>{
        item.checked = false;
      })
      this.isInvoiceSelected = false;
    }

  }

  getSelectedInvoice(invoice) {
    
    var length=0;

    this.totalAmountArray = [];
    this.selectedInvoiceIdArray = [];

    _.each(this.invoiceDataList, (item)=>{
        if(item.checked){
          length++;
          this.totalAmountArray.push(item.vendorInvoiceAmount);
          this.selectedInvoiceIdArray.push(item.vendorInvoiceId);
        }
     });

    if(length>0){
      this.isInvoiceSelected = true;
    }
    else{
      this.isInvoiceSelected = false;
      this.totalAmountArray = [];
      this.selectedInvoiceIdArray = [];
    }

    if(length==0){
      this.selectAllInvoices = false;
      this.totalAmountArray = [];
      this.selectedInvoiceIdArray = [];
    }

    if(length == this.invoiceDataList.length){
      this.selectAllInvoices = true;
    }


    
  }


  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  isCollectionMade(event){
    if(event)
      this.getCustInvoices();
  }



  getCustInvoices() {

    var params = {
      apartmentId: this.sessionService.apartmentId,
      vendorId: this.route.params['value'].id
    }

    this.accountsService.getVendorInvoiceByVendorId(params).subscribe((res:any) => {


      this.invoiceDataList = res;


      this.totalItems = this.invoiceDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isInvoiceDataLoaded = true;
   

    })

  }

  ngOnInit() {

  this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

  let cellsrenderer = (row, column, value) => {
    return '<div class="jqx-custom-inner-cell">' + value + '</div>';
  }

  let columnrenderer = (value) => {
    return '<div style="padding: 14px">' + value + '</div>';
  }

  this.columnData = [{
    text: '',
    datafield: 'checked',
    width: 80,
    pinned: true,
    sortable: false,
    menu: false,
    cellsrenderer: (row, column, value) => {

       var chkedProperty;
       
       if(value){
         chkedProperty = 'checked="true"';
       }
       else {
         chkedProperty = '';
       }

       return '<div class="jqx-custom-inner-cell">'
               + '<div class="form-group mb-0 w-100">'
                 +  '<div class="form-check text-center">'
                     + '<input type="checkbox" class="form-check-input" onClick="checkInvoiceRowEvent('+ row +', this.checked)" id="defaultChecker'+ row +'" name="defaultChecker'+ row +'" '+ chkedProperty +'>'
                     + '<label class="form-check-label" for="defaultChecker'+ row +'"></label>'
                 +  '</div>'
               + '</div>'
            + '</div>'
    },
    renderer: (value) => {

     return '<div style="padding: 10px">'
     + '<div class="form-group mb-0 w-100">'
       +  '<div class="form-check check-header text-center">'
           + '<input type="checkbox" id="selectAllDefaulters" name="selectAllDefaulters" onClick="checkInvoiceHeaderEvent(event, this.checked)" >'
           + '<label class="form-check-label" for="selectAllDefaulters"></label>'
       +  '</div>'
     + '</div>'
    + '</div>'

    }
  },
  {
    text: 'Account',
    datafield: 'vendorInvoiceGlaccount',
    width: 120,
    cellsrenderer: cellsrenderer,
    renderer: columnrenderer
  },
  {
    text: 'Invoice No',
    datafield: 'vendorInvoiceId',
    width: 120,
    cellsrenderer: cellsrenderer,
    renderer: columnrenderer
  },
  {
    text: 'Invoice Date',
    datafield: 'vendorInvoiceDate',
    width: 120,
    cellsrenderer: (row, column, value): string => {
      return '<div class="jqx-custom-inner-cell">' + moment.utc(value).tz(this.timeZone.region).format(this.timeZone.date) + '</div>'
    },
    renderer: columnrenderer
  },
  {
    text: 'Payee Name',
    datafield: 'comments',
    minwidth: 180,
    cellsrenderer: cellsrenderer,
    renderer: columnrenderer
  },
  {
    text: 'Due Date',
    datafield: 'dueDate',
    width: 120,
    cellsrenderer: (row, column, value): string => {
      return '<div class="jqx-custom-inner-cell">' + moment.utc(value).tz(this.timeZone.region).format(this.timeZone.date) + '</div>'
    },
    renderer: columnrenderer
  },
  {
    text: 'Bill Amount',
    datafield: 'vendorInvoiceAmount',
    minwidth: 120,
    cellsrenderer: cellsrenderer,
    renderer: columnrenderer
  },
  {
    text: 'Remaining Amount',
    datafield: 'remainingAmount',
    minwidth: 120,
    cellsrenderer: cellsrenderer,
    renderer: columnrenderer
  }];

  this.getCustInvoices();

  let params = {
    apartmentId: this.sessionService.apartmentId
  }

  this.vendorService.getVendorByApartmentId(params).subscribe((res:any) => {

      this.vendorDataList = res.filter(item => {
        return item.vendorId == this.route.params['value'].id;
      });

      this.isVendorDataLoaded = true;
      
      let categoryParams = {
        LookupTypeId: 57,
        ApartmentId: this.sessionService.apartmentId
      }

      this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res:any) => {

        this.vendorCategoryData = res.filter(item => {
            return item.isActive;
        });

      });


  });

	var accountParams = {
      apartmentId: this.sessionService.apartmentId,
      groupId: 3
  }

  this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {
    this.glAccountListData = res;
  });

  	

  }

}
