import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { ExpensePayInvoiceComponent } from './expense-pay-invoice.component';


export const routes: Routes = [
  { path: '', component: ExpensePayInvoiceComponent },
  { path: ':id', component: ExpensePayInvoiceComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpensePayInvoiceRoutingModule { }
